<template>
	<div id="auth-login">
		<div class="content">
			<div class="login-header">
				<div class="login-title">欢迎登陆</div>
			</div>
			<div class="login-group">
				<div class="login-avatar">
					<div class="avatar">
						<img class="img-100" :src="config.logo" alt="">
					</div>
				</div>
				<div class="login-input">
					<input v-model.number="mobile" class="c-gray login-info" type="text" placeholder="请输入手机号">
					<div class="flex-lr login-info">
						<input v-model="password" class="c-gray login-password" type="password" placeholder="请输入密码">
						<router-link tag="div" :to="util.getUrl({path: '/pages/auth/forget'})" class="forget">忘记密码？
						</router-link>
					</div>
				</div>
				<div class="login-button btn-group">
					<button class="font-16 van-button van-button--normal van-button--block " @click="onSubmit">登陆
					</button>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"

	export default {
		data() {
			return {
				config: {
					logo: ''
				},
				mobile: '',
				password: '',
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				var idtoken = that.util.getStorage('idtoken');
				if (idtoken) {
					that.$router.replace(that.util.getUrl({path: '/pages/order/takeout'}));
					return;
				}
				getData({
					vue: that,
					url: 'delivery/auth/login',
					autoAssign: true,
					variable: 'config',
				})
			},
			onSubmit() {
				var that = this;
				if (!that.mobile) {
					that.util.$toast('请输入手机号');
					return;
				}
				if (!that.password) {
					that.util.$toast('请输入密码');
					return;
				}
				setData({
					vue: that,
					url: 'delivery/auth/login',
					data: {
						mobile: that.mobile,
						password: that.password
					},
					success: function (res) {
						that.util.setStorage('idtoken', res.deliveryer.token);
						var redirct_url = window.redirct_url;
						if (!redirct_url || (redirct_url && redirct_url.path.indexOf('pages/auth/') != -1)) {
							redirct_url = {path: '/pages/order/takeout'};
						}
						that.$router.replace(that.util.getUrl(redirct_url));
						return;
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#auth-login .login-header {
		padding: 30px 15px 125px;
		background: url('../../../static/img/login-background.png') no-repeat 100%;
	}

	#auth-login .login-header .login-title {
		color: #fff;
		font-size: 25px;
		font-weight: 500;
	}

	#auth-login .login-group {
		position: absolute;
		top: 90px;
		left: 15px;
		right: 15px;
		background-color: #fff;
		border-radius: 5px;
		padding: 20px;
	}

	#auth-login .login-group .login-avatar {
		display: flex;
		justify-content: center;
		width: 100%;
		padding: 20px 0;
	}

	#auth-login .login-group .login-avatar .avatar {
		width: 80px;
		height: 80px;
	}

	#auth-login .login-group .login-avatar .avatar img {
		border-radius: 15px;
	}

	#auth-login .login-group .login-input {
		margin-top: 20px;
	}

	#auth-login .login-group .login-input .login-info {
		padding: 10px 0;
		width: 100%;
		height: 45px;
		line-height: 25px;
		font-size: 16px;
		border-bottom: 1px solid #eee;
	}

	#auth-login .login-group .login-input .login-info .login-password {
		width: 70%;
	}

	#auth-login .login-group .login-input .login-info .forget {
		font-size: 14px;
		color: #1AB5F5;
	}

	#auth-login .login-group .login-button {
		padding: 30px 0 15px;
	}

	#auth-login .login-group .login-button button {
		border: 1px solid #0EC3B3;
		background-color: #0EC3B3;
		color: #fff;
	}
</style>


